$base-color-1: rgb(164, 59, 162);
$base-color-2: rgb(54, 93, 179);
$base-color-3: rgb(62, 172, 62);


body {
    margin: 0;
    padding: 0;
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.container-1 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    height:20vh;
    gap: 5px;
    overflow: auto;


    .box {
        background-color: $base-color-1;
        display: flex;
        justify-content: center;
        align-content: center;
        color: bisque;
        padding: 10px;
        border-radius: 20%;
    }

    $i: 1;
    @while $i <= 4 {
        .lighten-#{$i} {
            filter: brightness($i/4+1); 
            
        }
        

        .darken-#{$i} {
            filter: brightness(1-$i/10);
        }

        $i: $i + 1;
    }
     
}

.container-2 {
    @extend .container-1;

    .box {
        background-color: $base-color-2;
    }
}

.container-3 {
    @extend .container-1;

    .box {
        background-color: $base-color-3;
    }
}